
.btn {
	&, &:link, &:visited{
		text-transform: uppercase;
		text-decoration: none;
		padding: 1.5rem 4rem;
		display: inline-block;
		border-radius: 10rem;
		/* untuk animation transform yang bawah */
		transition: all .2s;
		position: relative;
		font-size: $default-font-size;
		
		// change for the button element
		border: none;
		cursor: pointer;
	}
	
	&:hover {
		transform: translateY(-3px);
		/* x, y, blur */
		box-shadow: 0 1rem 2rem rgba($color-black, .2);
	}
	
	&:active, &:focus {
		outline: none;
		transform: translateY(-1px);
		box-shadow: 0 .5rem 1rem rgba($color-black, .2);
	}
	
	&::after {
		/* content harus di buat */
		content: "";
		display: inline-block;
		height: 100%;
		width: 100%;
		border-radius: 10rem;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		transition: all .4s;
	}
	
	&:hover::after {
		transform: scaleX(1.4) scaleY(1.6);
		opacity: 0;
	}
	
	&--white {
		background-color: $color-white;
		color: $color-grey-dark;
		
		&:after {
			background-color: $color-white;
		}
	}
	
	&--green {
		background-color: $color-primary;
		color: $color-white;
		
		&:after {
			background-color: $color-primary;
		}
	}
		
	&--animated {
		/* animation name, animation duration, animation timing function, animation delay */
		animation: moveInBottom .5s ease-out .75s;
		/* Let the element get the style values set by the first keyframe before the animation starts (during the animation-delay period, in this case 0% */
		animation-fill-mode: backwards;	
	}
}

.btn-text {
	&:link,
	&:visited {
		font-size: $default-font-size;
		color: $color-primary;
		display: inline-block;
		text-decoration: none;
		border-bottom: 1px solid $color-primary;
		padding: 3px;
		transition: all .2s;
	}
	
	&:hover {
		background-color: $color-primary;
		color: $color-white;
		box-shadow: 0 1rem 2rem rgba($color-black, .1s);
		transform: translateY(-2px);
		
	}
	
	&:active {
		box-shadow: 0 .5rem 1rem rgba($color-black, .1s);
		transform: translateY(0px);
	}
	
		
}