.composition {
	position: relative;
	height: 28rem;

	&__photo {
		width: 55%;
		box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
		border-radius: 2px;
		position: absolute;
		z-index: 1;
		transition: all .2s;

		// untuk membuat border di antara gambar
		outline-offset: 2rem;

		margin-top: 3rem;

		@include respond(tab-port) {
			float: left;
			position: relative;
			width: 33.333333%;
			box-shadow: 0 1.5rem 3rem rgba($color-black, .2);
		}

		&--p1 {
			left: 0;
			top: -2rem;

			@include respond(tab-port) {
				top: 0;
				transform: scale(1.2);
			}
		}

		&--p2 {
			right: 0;
			top: 2rem;

			@include respond(tab-port) {
				top: -1rem;
				transform: scale(1.3);
				z-index: 100;
			}

		}

		&--p3 {
			left: 20%;
			top: 10rem;

			@include respond(tab-port) {
				top: 1rem;
				left: 0;
				transform: scale(1.1);
			}
		}

		&:hover {
			// untuk membuat border di antara gambar
			outline: 1.5rem solid $color-primary;
			transform: scale(1.5) translateY(-.5px);
			box-shadow: 0 2.5rem 4rem rgba($color-black, .5);
			z-index: 10;
		}
	}

	//composition:hover composition__photo:not(:hover)

	&:hover &__photo:not(:hover) {
		transform: scale(.95);
	}



}

